import Vue from 'vue'
import VueRouter from 'vue-router'
const routerPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
    return routerPush.call(this, location).catch(error => error)
}

import Home from './views/Home.vue'
import Maincontent from './views/Maincontent.vue'
Vue.use(VueRouter)
export default new VueRouter({
    routes: [{
            path: '/',
            component: Home,
            children: [{
                path: '/main',
                component: Maincontent

            }, {
                path: '/Aboutus',
                component: () =>
                    import ('./views/homepages/Aboutus.vue')
            }, {
                path: '/Newscenter',
                component: () =>
                    import ('./views/homepages/Newscenter.vue'),
                children: [{
                    path: '/Newscenter/industry',
                    component: () =>
                        import ('./views/newspage/Industry.vue')
                }, {
                    path: '/Newscenter/online',
                    component: () =>
                        import ('./views/newspage/Online.vue')
                }, {
                    path: '/Newscenter/details',
                    component: () =>
                        import ('./views/newspage/details/Details.vue')
                }],
                redirect: '/Newscenter/industry'
            }, {
                path: '/Panter',
                component: () =>
                    import ('./views/homepages/Panter.vue')
            }, {
                path: '/downcenter',
                component: () =>
                    import ('./views/homepages/Downcenter.vue'),
                children: [{
                    path: '/down/table',
                    component: () =>
                        import ('./views/downpage/Downtable.vue')
                }],
                redirect: '/down/table'
            }],
            redirect: '/main'

        },

    ]
})