<template>
  <div>
    <div class="userInfo">
      <div class="userHead">最好的服务，献给最好的您</div>
      <div class="userDes">The best service for the best of you</div>
      <div class="intWrapper">
        <input type="text" v-model="name" class="sex" placeholder="姓名" />
        <input type="text" v-model="sex" class="name" placeholder="性别" />
        <input type="text" v-model="tel" class="tel" placeholder="电话" />
        <div @click="info()" class="subBtn">提交</div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from "axios";
export default {
  data() {
    return {
      name: "",
      sex: "",
      tel: ""
    };
  },
  methods: {
    info() {
      if (this.name == "") {
        alert("姓名不能为空");
      } else {
        if (this.tel == "") {
          alert("电话号码不能为空");
        } else {
          Axios.post("http://192.168.0.70:8084/openApi/us", {
            name: this.name,
            content: this.sex,
            phone: this.tel,
            type: "2"
          })
            .then(res => {
              // window.console.log(res);
              if (res.data.code == 200) {
                alert(res.data.msg);
              } else {
                alert(res.data.msg);
              }
            })
            .catch(err => {
              window.console.log(err);
              alert("提交失败");
            });
        }
      }
    }
  }
};
</script>

<style lang="less" scoped>
.userInfo {
  width: 1200px;
  margin: 0 auto;
  margin-bottom: 100px;
  .userHead {
    font-size: 48px;
    color: #354536;
    text-align: center;
    margin-bottom: 40px;
  }
  .userDes {
    font-size: 30px;
    color: #6ba933;
    margin-bottom: 106px;
    text-align: center;
  }
  .intWrapper {
    display: flex;
    input {
      padding-left: 22px;
      border: 1px solid rgba(53, 69, 54, 1);
      outline: none;
      font-size: 30px;
    }
    .sex {
      width: 168px;
      height: 80px;
      margin-right: 28px;
    }
    .name {
      width: 383px;
      margin-right: 27px;
    }
    .tel {
      width: 370px;
      margin-right: 22px;
    }
  }
  .subBtn {
    width: 200px;
    height: 82px;
    background: #6ba933;
    color: #fff;
    line-height: 82px;
    text-align: center;
    font-size: 30px;
  }
}
</style>