<template>
  <div>
    <div :class="['indexHeaderWapper',{'changeTop':startscroll}]">
      <div :class="{'scrollWrapper':startscroll}">
        <div :class="{'scrollTop':startscroll}">
          <div class="leftLogo">
            <!-- <img v-show="!startscroll" src="../assets/imgs/logo.svg" alt="!" /> -->
            <img src="../assets/imgs/AGP-logo@2x.png" alt />
          </div>
          <div class="TopNav">
            <ul>
              <li
                @mousemove="changehoverin('/main')"
                :class="{'active':'/main'==value}"
                @click="changeView('/main')"
              >首页</li>
              <li
                @mousemove="changehoverin('/Aboutus')"
                :class="{'active':'/Aboutus'==value}"
                @click="changeView('/Aboutus')"
              >关于我们</li>
              <li
                @mousemove="changehoverin('/Newscenter')"
                :class="{'active':'/Newscenter'==value}"
                @click="changeView('/Newscenter')"
              >新闻中心</li>
              <li
                @mousemove="changehoverin('/')"
                @click="changeView('borwe')"
                :class="{'active':'/'==value}"
              >区块浏览器</li>

              <li
                @mousemove="changehoverin('/Panter')"
                :class="{'active':'/Panter'==value}"
                @click="changeView('/Panter')"
              >合作伙伴</li>
            </ul>
            <div @click="changeView('/downcenter')" class="downCenter">下载中心</div>
          </div>
        </div>
      </div>
    </div>
    <router-view></router-view>
    <Bottom></Bottom>
  </div>
</template>

<script>
import Bottom from "../components/Bottom";
export default {
  components: {
    Bottom
  },
  created() {
    this.path = this.$route.path;
  },

  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    this.value = this.$route.path;
  },

  data() {
    return {
      value: "/main",
      startscroll: false,
      loadingLine: false
    };
  },
  updated: function() {
    this.loadingLine = true;
  },
  methods: {
    handleScroll() {
      let scrollY = window.scrollY;
      // window.console.log(scrollY);
      if (scrollY > 100) {
        this.startscroll = true;
      } else {
        this.startscroll = false;
      }
    },
    changehoverin(p) {
      this.value = p;
    },
    // changehoverout(p){
    //   this.value=p
    // },
    changeView(path) {
      if (path == "borwe") {
        window.location.href = "http://ex.agpchain.io/";
      } else {
        this.$router.push(path);
        this.value = path;
      }
    }
  }
};
</script >

<style lang="less" scoped>
.indexHeaderWapper {
  position: absolute;
  width: 100%;
  z-index: 999;
  top: 20px;
  height: 141px !important;
  padding: 0 !important;
  transition: all 0.45 ease-in-out;

  & > div > div {
    margin: 0 auto;
    width: 1200px;
    display: flex;
    justify-content: space-between;
    background-color: transparent;
    height: 100px;
    z-index: 9999;

    .TopNav {
      display: flex;
      align-items: center;
      ul {
        list-style: none;
        display: flex;
        width: 603px;
        justify-content: space-between;
        li {
          color: #333333;
          font-size: 16px;
          line-height: 60px;
          cursor: pointer;
        }
      }
      .downCenter {
        line-height: 39px;
        width: 91px;
        height: 39px;
        background: #016737;
        border-radius: 5px;
        color: #fff;
        font-size: 16px;
        text-align: center;
        margin-left: 40px;
        cursor: pointer;
      }
    }
  }

  .scrollWrapper {
    background: #fff;
    border-top: 2px solid #016737;
  }
  .scrollTop {
    padding: 0;
    .TopNav {
      ul {
        li {
          color: rgba(0, 0, 0, 0.6);
        }
      }
    }
  }
  .leftLogo {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    img {
      width: 128px;
      height: 39px;
    }
  }

  a {
    text-decoration: none;
  }
}
.changeTop {
  position: fixed;
  top: 0px !important;
}
.active {
  border-bottom: 2px solid #016737;
  color: #016737 !important;
}
.activenext {
  border-bottom: 2px solid #016737;
}
</style>