<template>
  <div class="Wrapper">
    <div class="ourdata">
      <div class="datahead">我们的数据</div>
      <div class="dataen">Our data</div>
      <div class="dataWrapper">
        <div class="agp">
          <img src="../assets/imgs/data1@2x.png" alt />
          <p>AGP</p>
          <p>
            <countTo ref="numT1" :startVal="startVal" :endVal="endVal1" :duration="4000"></countTo>
          </p>
        </div>
        <div class="user">
          <img src="../assets/imgs/data2@2x.png" alt />
          <p>用户群</p>
          <p>
            <countTo ref="numT2" :startVal="startVal" :endVal="endVal2" :duration="4000"></countTo>
          </p>
        </div>
        <div class="dwon">
          <img src="../assets/imgs/data3@2x.png" alt />
          <p>下载量</p>
          <p>
            <countTo ref="numT3" :startVal="startVal" :endVal="endVal3" :duration="4000"></countTo>
          </p>
        </div>
        <div class="node">
          <img src="../assets/imgs/deta4@2x.png" alt />
          <p>节点</p>
          <p>
            <countTo ref="numT4" :startVal="startVal" :endVal="endVal4" :duration="4000"></countTo>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import countTo from "vue-count-to";

export default {
  components: { countTo },
  mounted() {
    window.addEventListener("scroll", this.datascroll);
    this.value = this.$route.path;
  },
  data() {
    return {
      startVal: 0,
      endVal1: 10000,
      endVal2: 8724,
      endVal3: 6721,
      endVal4: 21
    };
  },
  methods: {
    datascroll() {
      let scrollY = window.scrollY;
      // window.console.log(scrollY);
      if (scrollY == 3200) {
        this.$refs.numT1.start();
        this.$refs.numT2.start();
        this.$refs.numT3.start();
        this.$refs.numT4.start();
      }
    }
  }
};
</script>

<style lang="less" scoped>
.Wrapper {
  width: 1200px;
  margin: 0 auto;
  .ourdata {
    .datahead {
      font-size: 48px;
      color: #354536;
      text-align: center;
      margin-bottom: 19px;
    }
    .dataen {
      font-size: 30px;
      color: #6ba933;
      margin-bottom: 118px;
      text-align: center;
    }
    .dataWrapper {
      display: flex;
      justify-content: space-between;
      margin-bottom: 210px;
      & > div {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        img {
          width: 174px;
          height: 174px;
          margin-bottom: 58px;
        }
        & > p:nth-child(2) {
          color: #333333;
          font-size: 26px;
          margin-bottom: 30px;
        }
        & > p:nth-child(3) {
          color: #333333;
          font-size: 50px;
        }
      }
    }
  }
}
</style>