<script>
import Ourdata from "../components/Ourdata";
import Serve from "../components/Serve";
import { getNews } from "../api.js";
import Axios from "axios";
export default {
  mounted() {
    getNews(1, 2, "desc", "id", 1)
      .then(res => {
        this.news = res.data.rows;
      })
      .catch(err => {
        window.console.log(err);
      });
    getNews(1, 3, "desc", "id", 2)
      .then(res => {
        window.console.log(res.data.rows);
        this.notice = res.data.rows;
      })
      .catch(err => {
        window.console.log(err);
      });
  },
  components: {
    Ourdata,
    Serve
  },
  data() {
    return {
      ios: true,
      android: false,
      news: [],
      notice: []
    };
  },
  methods: {
    changeshow(n) {
      if (n == "ios") {
        this.ios = true;
        this.android = false;
      } else {
        this.ios = false;
        this.android = true;
      }
    },
    changeView() {
      this.$router.push("/downcenter");
    },
    toNews(p) {
      this.$router.push(p);
    },
    openbook() {
      Axios.get("http://159.138.131.239:8084/prod-api/openApi/paper/1")
        .then(res => {
          // window.console.log(res);
          window.open(res.data.data[0].downUrl);
        })
        .catch(err => {
          window.console.log(err);
        });
    }
  }
};
</script>

