import Axios from "axios";
export function getNews(num, size, desc, id, type) {
    return Axios.get('http://159.138.131.239:8084/prod-api/openApi/news/list', {
        params: {
            pageNum: num,
            pageSize: size,
            isAsc: desc,
            id: id,
            type: type,
            orderByColumn: 'sort'
        }
    })
}
export function newsContent(id, type) {
    return Axios.get('http://159.138.131.239:8084/prod-api/openApi/news/' + id + '?type=' + type)
}